import React, { useState } from 'react'

const Stepmilestones = ({ onNext, onBack }) => {
    const [fields, setFields] = useState([{ id: 1 }]);
    const [showRemoveBtn, setShowRemoveBtn] = useState(false);

    const addField = () => {
        const newFields = [...fields, { id: fields.length + 1 }];
        setFields(newFields);
        setShowRemoveBtn(true);
    };

    const removeField = (id) => {
        const updatedFields = fields.filter((field) => field.id !== id);
        setFields(updatedFields);
        setShowRemoveBtn(updatedFields.length > 1);
    };
    return (
        <section className='verifytoken'>
            <h6 className="verifyhead">Milestones</h6>
            {fields.map((field, index) => (
                <>
                    <div key={field.id} className="new-fields">
                        <div class="material-textfield newinnerfield">
                            <input placeholder="Milestone" type="text" />
                            <label>Milestone</label>
                        </div>
                        <div class="material-textfield">
                            <input placeholder="Date" type="text" />
                            <label>Date</label>
                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="19" viewBox="0 0 18 19" fill="none" className='calendarimg'>
                                <path d="M12.5625 3.17V2C12.5625 1.6925 12.3075 1.4375 12 1.4375C11.6925 1.4375 11.4375 1.6925 11.4375 2V3.125H6.56255V2C6.56255 1.6925 6.30755 1.4375 6.00005 1.4375C5.69255 1.4375 5.43755 1.6925 5.43755 2V3.17C3.41255 3.3575 2.43005 4.565 2.28005 6.3575C2.26505 6.575 2.44505 6.755 2.65505 6.755H15.345C15.5625 6.755 15.7425 6.5675 15.72 6.3575C15.57 4.565 14.5875 3.3575 12.5625 3.17Z" fill="#3456D0" />
                                <path d="M15 7.87988H3C2.5875 7.87988 2.25 8.21738 2.25 8.62988V13.2499C2.25 15.4999 3.375 16.9999 6 16.9999H12C14.625 16.9999 15.75 15.4999 15.75 13.2499V8.62988C15.75 8.21738 15.4125 7.87988 15 7.87988ZM6.9075 14.1574C6.87 14.1874 6.8325 14.2249 6.795 14.2474C6.75 14.2774 6.705 14.2999 6.66 14.3149C6.615 14.3374 6.57 14.3524 6.525 14.3599C6.4725 14.3674 6.4275 14.3749 6.375 14.3749C6.2775 14.3749 6.18 14.3524 6.09 14.3149C5.9925 14.2774 5.9175 14.2249 5.8425 14.1574C5.7075 14.0149 5.625 13.8199 5.625 13.6249C5.625 13.4299 5.7075 13.2349 5.8425 13.0924C5.9175 13.0249 5.9925 12.9724 6.09 12.9349C6.225 12.8749 6.375 12.8599 6.525 12.8899C6.57 12.8974 6.615 12.9124 6.66 12.9349C6.705 12.9499 6.75 12.9724 6.795 13.0024C6.8325 13.0324 6.87 13.0624 6.9075 13.0924C7.0425 13.2349 7.125 13.4299 7.125 13.6249C7.125 13.8199 7.0425 14.0149 6.9075 14.1574ZM6.9075 11.5324C6.765 11.6674 6.57 11.7499 6.375 11.7499C6.18 11.7499 5.985 11.6674 5.8425 11.5324C5.7075 11.3899 5.625 11.1949 5.625 10.9999C5.625 10.8049 5.7075 10.6099 5.8425 10.4674C6.0525 10.2574 6.3825 10.1899 6.66 10.3099C6.7575 10.3474 6.84 10.3999 6.9075 10.4674C7.0425 10.6099 7.125 10.8049 7.125 10.9999C7.125 11.1949 7.0425 11.3899 6.9075 11.5324ZM9.5325 14.1574C9.39 14.2924 9.195 14.3749 9 14.3749C8.805 14.3749 8.61 14.2924 8.4675 14.1574C8.3325 14.0149 8.25 13.8199 8.25 13.6249C8.25 13.4299 8.3325 13.2349 8.4675 13.0924C8.745 12.8149 9.255 12.8149 9.5325 13.0924C9.6675 13.2349 9.75 13.4299 9.75 13.6249C9.75 13.8199 9.6675 14.0149 9.5325 14.1574ZM9.5325 11.5324C9.495 11.5624 9.4575 11.5924 9.42 11.6224C9.375 11.6524 9.33 11.6749 9.285 11.6899C9.24 11.7124 9.195 11.7274 9.15 11.7349C9.0975 11.7424 9.0525 11.7499 9 11.7499C8.805 11.7499 8.61 11.6674 8.4675 11.5324C8.3325 11.3899 8.25 11.1949 8.25 10.9999C8.25 10.8049 8.3325 10.6099 8.4675 10.4674C8.535 10.3999 8.6175 10.3474 8.715 10.3099C8.9925 10.1899 9.3225 10.2574 9.5325 10.4674C9.6675 10.6099 9.75 10.8049 9.75 10.9999C9.75 11.1949 9.6675 11.3899 9.5325 11.5324ZM12.1575 14.1574C12.015 14.2924 11.82 14.3749 11.625 14.3749C11.43 14.3749 11.235 14.2924 11.0925 14.1574C10.9575 14.0149 10.875 13.8199 10.875 13.6249C10.875 13.4299 10.9575 13.2349 11.0925 13.0924C11.37 12.8149 11.88 12.8149 12.1575 13.0924C12.2925 13.2349 12.375 13.4299 12.375 13.6249C12.375 13.8199 12.2925 14.0149 12.1575 14.1574ZM12.1575 11.5324C12.12 11.5624 12.0825 11.5924 12.045 11.6224C12 11.6524 11.955 11.6749 11.91 11.6899C11.865 11.7124 11.82 11.7274 11.775 11.7349C11.7225 11.7424 11.67 11.7499 11.625 11.7499C11.43 11.7499 11.235 11.6674 11.0925 11.5324C10.9575 11.3899 10.875 11.1949 10.875 10.9999C10.875 10.8049 10.9575 10.6099 11.0925 10.4674C11.1675 10.3999 11.2425 10.3474 11.34 10.3099C11.475 10.2499 11.625 10.2349 11.775 10.2649C11.82 10.2724 11.865 10.2874 11.91 10.3099C11.955 10.3249 12 10.3474 12.045 10.3774C12.0825 10.4074 12.12 10.4374 12.1575 10.4674C12.2925 10.6099 12.375 10.8049 12.375 10.9999C12.375 11.1949 12.2925 11.3899 12.1575 11.5324Z" fill="#3456D0" />
                            </svg>
                        </div>
                    </div>
                    {index !== fields.length - 1 && showRemoveBtn && (
                        <button className="removebtn" onClick={() => removeField(field.id)}>
                            Remove
                        </button>
                    )}
                </>
            ))}
            <button className="addmorebtn" onClick={addField}>
            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 18 18" fill="none">
<path d="M13.5 9.5625H4.5C4.1925 9.5625 3.9375 9.3075 3.9375 9C3.9375 8.6925 4.1925 8.4375 4.5 8.4375H13.5C13.8075 8.4375 14.0625 8.6925 14.0625 9C14.0625 9.3075 13.8075 9.5625 13.5 9.5625Z" fill="#3456D0"/>
<path d="M9 14.0625C8.6925 14.0625 8.4375 13.8075 8.4375 13.5V4.5C8.4375 4.1925 8.6925 3.9375 9 3.9375C9.3075 3.9375 9.5625 4.1925 9.5625 4.5V13.5C9.5625 13.8075 9.3075 14.0625 9 14.0625Z" fill="#3456D0"/>
</svg>
                Add More
            </button>

            <div className="stepbuttons">
                <button className="dullbtn" onClick={onBack}>
                    Back
                </button>
                <button className="stepbtn" onClick={onNext}>
                    Next
                </button>
            </div>
        </section>
    )
}

export default Stepmilestones